<template>
  <button class="button" @click="handleClick">
    <span class="label">{{ props.label }}</span>
  </button>
</template>

<script setup lang="ts">
const props = defineProps({
  label: String,
  textColor: {
    type: String,
    default: "#ffffff",
  },
  fontSize: {
    type: String,
    default: "14px",
  },
  backgroundColor: {
    type: String,
    default: "var(--primary)",
  },
  onInteraction: {
    type: Function,
    required: false,
  },
});
// eslint-disable-next-line @typescript-eslint/no-unused-vars
const handleClick = (event: Event) => {
  if (props.onInteraction) {
    props.onInteraction("click");
  }
};
</script>

<style scoped>
.label {
  font-family: Poppins;
  display: inline-block;
  font-size: v-bind(fontSize);
}

.button {
  all: unset;
  width: 166px;
  height: 48px;
  background: v-bind(backgroundColor) 0% 0% no-repeat padding-box;
  border-radius: 25px;
  text-align: center;
  color: v-bind(textColor);
}

.button:hover {
  color: v-bind(backgroundColor);
  cursor: pointer;
}

.button {
  box-shadow: 0 0 1px rgb(0 0 0 / 0%);
  z-index: 1;
  position: relative;
  font-size: inherit;
  font-family: inherit;
  color: v-bind(textColor);
  outline: none;
  border: none;
  overflow: hidden;
  transition: color 0.2s ease-in-out;
}

.button::before {
  content: "";
  z-index: -1;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  background-color: v-bind(textColor);
  transform-origin: center;
  transform: translate3d(-50%, -50%, 0) scale3d(0, 0, 0);
  transition: transform 0.25s ease-in-out;
}

.button:hover::before {
  transform: translate3d(-50%, -50%, 0) scale3d(15, 15, 15);
}
</style>
